import React, { useEffect, useReducer, useCallback, useState } from "react";
import "./css/DonePage.css"
import { useNavigation } from "../Router/Router";
const DonePage = () => {
  const { onClickStart, onClickOwner, onClickPay, onClickDone, onClickMain } =
    useNavigation();
  const [ count, setCount ] = useState(10);
  const [alert,setAlert] = useState(false);
  useEffect(() => {
    const interval = setInterval(() => {
      setCount((count) => count - 1);
    }, 1000);
  
    setTimeout(() => {
      clearInterval(interval); // setInterval 
      onClickMain();
    }, 10000);
  
    return () => clearInterval(interval); //  unmount  clearInterval 
  }, []);

  
  return (
    <>
      <header>
      <div className="Header2">
              <div className="goods2">1. </div>
              <div className="pay2">2.  </div>
              <div className="done2">3.  </div>
            </div>
      </header>
      <div className="background">
        <div className="string">
         !  <br/><span style={{fontSize:'45px',color: '#d24232'}}>{count}    </span>
        </div>
        
      </div>
    </>
  );
};
export default DonePage;
